<template>
   <div id="generadorCat" class="container mb-5">
      <h1 class="titulo mb-5">{{ msg }}</h1>
      <form @submit.prevent="generarGato">
         <div class="input-group input-group-lg mb-3">
            <div class="input-group-prepend">
               <span class="input-group-text">Texto:</span>
            </div>
            <input type="text" class="form-control" placeholder="Texto" aria-label="Text" v-model="texto">
         </div>

         <div class="input-group input-group-lg mb-3">
               <div class="input-group-prepend">
                  <span class="input-group-text">Tamaño:</span>
               </div>
               <input type="number" class="form-control" placeholder="Tamaño del texto" aria-label="Tamano" v-model="tamano">
               <span class="input-group-text">px</span>
         </div>

         <div class="input-group input-group-lg mb-3">
            <div class="input-group-prepend">
               <label class="input-group-text">Filtro:</label>
            </div>
            <select class="custom-select" name="filter" id="filter" v-model="filtro">
               <option selected="selected">Elige un filtro...</option>
               <option value="blur">Desenfocado</option>
               <option value="mono">Blanco y negro</option>
               <option value="sepia">Sepia</option>
               <option value="negative">Negativo</option>
               <option value="paint">Pintura</option>
               <option value="pixel">Pixel</option>
            </select>
         </div>

         <div class="input-group input-group-lg mb-3">
            <div class="input-group-prepend">
               <label class="input-group-text">Color:</label>
            </div>      
            <select class="custom-select" v-model="color">
               <option selected>Elige un color...</option>
               <option value="white">Blanco</option>
               <option value="black">Negro</option>
               <option value="blue">Azul</option>
               <option value="red">Rojo</option>
               <option value="yellow">Amarillo</option>
               <option value="purple">Violeta</option>
               <option value="orange">Naranja</option>
               <option value="green">Verde</option>
            </select>
            <span class="circulo ml-3" :style="{backgroundColor : color}"></span>
         </div>

         <button type="button" class="btn btn-purple btn-lg my-4" @click="generarGato">Generar Gatito Random</button>
      </form>

      <div class="gatoGenerado text-center my-5" v-if="urlGenerado">
         <h3 class="subtitulo mt-3">Resultado: </h3>
         
         <img :src="urlGenerado" alt="gatito" class="mx-auto">
      </div>
   </div>
</template>

<script>
export default {
  name: 'RandomCat',
  props: {
    msg: String,
    baseurl: String,
  },
  data(){
     return {
        texto: '',
        filtro: '',
        color: '',
        tamano: '',
        urlGenerado: ''
     }
   },
   methods: {
      generarGato() {
         if(this.texto || this.filtro || this.color || this.tamano) {
            fetch(this.baseurl+"/says/"+this.texto+"?filter="+this.filtro+"&color="+this.color+"&width="+this.tamano)
            .then(data =>{
            console.log(data.url);
            this.urlGenerado = data.url;
            })
            .catch(error => console.error(error));
         }
         else {
            alert("No has ingresado ningún dato.");
         }
      }
  },
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Nerko+One&display=swap');
#generadorCat{
   width: 70%;
   position: relative;
}
.gatoGenerado img{
   border: 6px groove deeppink;
   padding: 10px;
}
.titulo {
   font-family: 'Nerko One', cursive;
   font-size: 80px;
   color: rgb(241, 85, 202);
}
.subtitulo {
   font-family: 'Nerko One', cursive;
   font-size: 40px;
   color: black;
}
.circulo{
   width: 45px;
   height: 45px;
   border-radius: 50px;
   border: 1px solid whitesmoke;
   background-color: transparent;
}
.btn-purple{
   color: #fff !important;
   background-color: deeppink !important;
   border-color: deeppink !important;
}
</style>